.boton-siguiente{
    width: 200px;
    height: 60px;
    color: white;
    background-color: red;
    margin: 0 auto;
    border: none;
    border-radius: 25px;
    transition: transform 0.3s ease;
    display: flex;
    justify-content: center;
    align-items: center;

}


.boton-siguiente:hover{
    transform: scale(1.2);
    transition: 0.3s ease all;
    font-size: 500;
}
    
@media (min-width:320px) and (max-width:768px){
.boton-siguiente{
   margin: 0 auto;

    margin-bottom: 40px;
   /* margin-left: 0;*/
   
}
}