.portfolio {
  padding: 40px 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.portfolio h1 {
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  margin-bottom: 30px;
  color: red;
}

/* Grid de proyectos */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 24px;
}

/* Responsive */
@media (max-width: 768px) {
  .portfolio {
    padding: 20px 10px;
  }

  .portfolio h1 {
    font-size: 1.5rem;
  }
}
