.input-base{
height: 50px;
    width: 80%;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    text-indent: 55px;
    background-color: white;
    transition: all 0.3s ease;
}

.input-valido {
  border-color: #28a745;
  background-color: #d4edda;
}

.input-error {
  border-color: #dc3545;
  background-color: #f8d7da;
}

