.about-contenedor{
    width:100%;
   font-family: 'Poppins', sans-serif;
    font-weight: bolder;
    margin-top:80px;
    border-radius: 25px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    
}



h2{
    font-weight: bold;
    text-align: center;
    width: 100%;
    padding-top: 50px;
    padding-bottom: 20px;
    color: red;
    font-size: 36px;
}




.about{
    
    text-align: center;
    font-size: 20px;
    width: 100%;
    color: white;
    
    


}


.about-info{
    width: 50%;
    
    
}


.conocimientos-contenedor{
    width: 40%;
    
}


@media (min-width:320px) and (max-width:768px){
    h2{
        font-size: 20px;
        padding-top: 10px;
    }


    .about{
    
    text-align: center;
    font-size: 12px;

}

.about-info{
    width: 100%;
    text-align: center;
    padding-left: 10px;
    padding-right: 10px;
}

.conocimientos-contenedor{
    width: 100%;
    
}
}