.menu-wrapper {
    position: relative;
    z-index: 999;
}

.hamburger-menu {
       display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 35px;
    height: 25px;
    cursor: pointer;
    z-index: 1010;
}

.hamburger-menu .bar {
    width: 100%;
    height: 4px;
    background-color: white;
    transition: 0.3s ease-in-out;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.3s ease-in-out, visibility 0.3s;
}

.menu {
    position: fixed;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100%;
    background-color: #333;
    transition: transform 0.5s ease-in-out;
    z-index: 1000;
}

.menu.open {
    transform: translateX(-100%);
}

.menu ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    list-style: none;
    text-align: center;
}

.menu li {
    padding: 20px;
}

.menu a {
    color: white;
    text-decoration: none;
    font-size: 24px;
    text-transform: uppercase;
    letter-spacing: 2px;
    transition: color 0.3s;
}

.menu a:hover {
    color: #ff6600;
}

.hamburger-menu.open .bar:nth-child(1) {
    transform: translateY(9px) rotate(45deg);
    visibility: hidden;
}

.hamburger-menu.open .bar:nth-child(2) {
    opacity: 0;
}

.hamburger-menu.open .bar:nth-child(3) {
    transform: translateY(-9px) rotate(-45deg);
    visibility: hidden;
}



@media (min-width:769px){
.menu-wrapper{
    visibility: hidden;

}


}
