nav {
    --_clr-txt: rgb(255, 255, 255);
    --_clr-txt-svg: rgb(147, 158, 184);
    --_ani-speed: 6s; /* speed of rotating text */

    display: flex;
    /*flex-wrap: wrap;*/
    gap: 4rem;
    font-size: 1.4rem;
    width: 100%;
    justify-content: center;
    align-items: center;
  
}
nav>.boton {
    position: relative;
    display: grid;
    place-content: center;
    grid-template-areas: 'stack';
    padding: 0 1.5rem;
    text-transform: uppercase;
    font-weight: 300;
    background-color: transparent;
    cursor: pointer;
    font-family: inherit;
    font-size: inherit;
    line-height: normal;
    overflow: visible;
    color: white;
    border-color: transparent;
}

/* place button items on top of each other */
nav>.boton>span {
    transition: all 300ms ease-in-out;
    grid-area: stack;
}
/* nav icon */
nav>.boton>span:last-of-type {
    margin-top: 0.25rem;
    transform: scale(0);
    transition-delay: 0ms;
    border-radius: 50%;
}
/* hover - hide text */
nav>.boton:focus-visible>span:first-of-type,
nav>.boton:hover>span:first-of-type {
    transform: scale(0);
}
/* hover - reveal icon */
nav>.boton:focus-visible>span:last-of-type,
nav>.boton:hover>span:last-of-type {
    transform: scale(1);
}

/* nav SVG circular text*/
nav>.boton>svg {
    position: absolute;
    width: 200px;
    height: 200px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transform-origin: center;
    opacity: 0;
    text-transform: uppercase;
    transition: all 300ms ease-in-out;
    color: var(--_clr-txt-svg);
}
/* hover - reveal rotating SVG */
nav>.boton:focus-visible>svg,
nav>.boton:hover>svg {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
    transition-delay: 150ms;
    transition: all 300ms ease-in-out;
}
