.cabecera {
  /* Escala de 40px (en 320px) hasta 120px (en 720px o más) */
  font-size: clamp(40px, calc(40px + (80 * ((100vw - 320px) / (720 - 320)))), 120px);
}

.cabecera2 {
  /* Escala de 16px (en 320px) hasta 30px (en 720px o más) */
  font-size: clamp(16px, calc(16px + (14 * ((100vw - 320px) / (720 - 320)))), 30px);
}


nav {
display: none;
