

.avatar {
  display: flex;
  align-items: center;
  justify-content: center;         
  width: clamp(255px, 40%, 450px);   /* mínimo 255px, máximo 450px, fluido con el 40% del marco */
  height: clamp(360px, 40%, 400px);                 
  border-radius: 50px;
  margin: 0 auto;  
  border: 2px solid #333; /* Borde de 2px, sólido y gris oscuro */
  box-shadow: 0 0 0 5px #5b8c00; /* Borde de color verde con 5px de grosor */
}

