.radial-container {
  position: relative;
  width: 300px;
  height: 300px;
  margin: 50px auto;
  display: flex;
  justify-content: center;
  align-items: center;
}


.power-button-wrapper {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  cursor: pointer;
  background-color: #2a9d8f;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
  position: relative; /* necesario para z-index */
  z-index: 10;        /* siempre encima */
}

.power-button-wrapper.active {
  transform: scale(1.1);
  background-color: #e63946;
}


.power-button-wrapper:hover {
  box-shadow: 0 0 20px #2a9d8f;
}

