/* Íconos radiales */
.radial-item {
  position: absolute;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

.radial-item img {
  width: 60%;
  height: 60%;
  object-fit: contain;
  pointer-events: auto; /* clicable */
}



.power-button-img {
  width: 60px;
  height: 60px;
  pointer-events: none;
  user-select: none;
  object-fit: contain;
}
