@import url('https://fonts.googleapis.com/css2?family=Clicker+Script&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.presentacion-contenedor{
    width: 100%;
    height: 400px;
    font-family: 'Times New Roman', Times, serif;
    margin-top:80px;
    display: flex;
    flex-wrap: wrap;
    
    

}
.contexto{
    display: flex;
    flex-direction: column;
    width: 50%;
    padding-left: 100px;
    
    

}
.texto-saludo{
    font-size: 30px;
    width:100% ;
    color: white;
    font-family: 'Poppins', sans-serif;
    font-weight: bolder;
    padding-bottom: 20px;
}

.nombre{
color: red;
font-family: 'Poppins',sans-serif;
}


.texto-profesion{
     color: white;
    font-family: 'Poppins', sans-serif;
    font-weight: bolder;
    font-size: 32px;
}







.texto{
 color: white;
    font-family: 'Poppins', sans-serif;
    font-weight: bolder;
    font-size: 20px;
}


button{
    font-family: "Space Mono", monospace;
    font-weight: bold;
    font-size: 400;
    

}



@media (min-width:320px) and (max-width:768px){
.presentacion-contenedor{
    width: 100%;
    height: auto;
    font-family: 'Times New Roman', Times, serif;
    margin-top:80px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    flex-direction: column;
    

}

.contexto{
    padding-left: 0;
    width: 90%;
    
}
.texto-saludo{
    font-size: 20px;
    
}



.texto-profesion{
    font-size: 12px;
}
.texto{
    font-size: 12px;
}
}